if (window.cordova) {
  document.addEventListener("deviceready", onDeviceReady, false);

  function onDeviceReady() {
    alert("cordova is ready");
    new Vue({
      el: '#app',
      router,
      components: {
        App
      },
      template: '<App/>'
    })
  }
} else {
  alert("welcome")
  new Vue({
    el: '#app',
    router,
    // vuex,
    components: {
      App
    },
    template: '<App/>'
  })

}

