import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Map from '@/components/Map'
import Test from '@/components/Test'
import QRcode from '@/components/QRcode'
import Picture from '@/components/Picture'
import Shake from '@/components/Shake'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      redirect: '/Home'
    }, {
      path: '/Home',
      name: 'Home',
      component: Home
    }, {
      path: '/Map',
      name: 'Map',
      component: Map
    }, {
      path: '/Test',
      name: 'Test',
      component: Test
    }, {
      path: '/QRcode',
      name: 'QRcode',
      component: QRcode
    }, {
      path: '/Picture',
      name: 'Picture',
      component: Picture
    }, {
      path: '/Shake',
      name: 'Shake',
      component: Shake
    }

  ]
})



