<template>
<div>

  <mt-header fixed >
    <mt-button icon="back" slot="left" @click.native="back">返回</mt-button>
    <mt-button icon="more" slot="right" @click.native="actionSheet"></mt-button>
</mt-header>

<div id="container" class="mymap"></div>

<mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>

</div>

</template>


<script>
import $ from "jquery";
// import AMap from "AMap";

export default {
  data() {
    return {
      actions: [
        {
          name: "定位",
          method: this.getPosition
        },
        {
          name: "跟踪",
          method: this.watchPosition
        },
        {
          name: "停止跟踪",
          method: this.clearPositionWatch
        },
        {
          name: "test",
          method: this.coordinateConvert
        }
      ],
      sheetVisible: false,
      watchID: "",
      optionsWatch: {
        maximumAge: 3600000,
        timeout: 30000,
        enableHighAccuracy: true
      },
      marker: null,
      map: null
    };
  },
  mounted() {
    let map = new window.AMap.Map("container", {
      resizeEnable: true,
      zoom: 15
    });
    AMap.plugin(["AMap.ToolBar", "AMap.Scale"], function() {
      map.addControl(new AMap.ToolBar());
      map.addControl(new AMap.Scale());
    });
    this.map = map;
    // setTimeout(function() {
    //   let marker = new AMap.Marker({ position: [116.480983, 39.989628] });
    //   marker.setMap(map);
    //   this.map = map;
    //   this.marker = marker;
    // });
    // this.loadmap();
  },
  methods: {
    actionSheet: function() {
      this.sheetVisible = true;
    },
    // loadmap() {
    //   alert(1);
    //   console.log(this.map);
    //   console.log(this.marker);
    //   marker.setMap(map);
    //   AMap.plugin(["AMap.ToolBar", "AMap.Scale"], function() {
    //     map.addControl(new AMap.ToolBar());
    //     map.addControl(new AMap.Scale());
    //   });
    // },

    loadMarker: function(latitude, longitude) {
      console.log(latitude);
      console.log(longitude);
      let that = this;
      this.marker = new AMap.Marker({
        position: [latitude, longitude]
      });
      this.marker.setMap(that.map);
    },

    back() {
      this.$router.go(-1);
    },

    getPosition: function() {
      let that = this;
      // var options = {
      //     maximumAge: 3000,
      //     timeout: 5000,
      //     enableHighAccuracy: true
      // }

      navigator.geolocation.getCurrentPosition(
        that.onSuccessGet,
        that.onErrorGet,
        {
          maximumAge: 30000,
          timeout: 30000,
          enableHighAccuracy: true
        }
      );
    },
    onSuccessGet: function(position) {
      // alert(
      //   "Latitude: " +
      //     position.coords.latitude +
      //     "\n" +
      //     "Longitude: " +
      //     position.coords.longitude +
      //     "\n" +
      //     "Altitude: " +
      //     position.coords.altitude +
      //     "\n" +
      //     "Accuracy: " +
      //     position.coords.accuracy +
      //     "\n" +
      //     "Altitude Accuracy: " +
      //     position.coords.altitudeAccuracy +
      //     "\n" +
      //     "Heading: " +
      //     position.coords.heading +
      //     "\n" +
      //     "Speed: " +
      //     position.coords.speed +
      //     "\n" +
      //     "Timestamp: " +
      //     position.timestamp +
      //     "\n"
      // );
      console.log(position);
      // this.loadMarker(position.coords.latitude, position.coords.longitude);
      let that = this;
      let map = that.map;
      if (that.marker) {
        // alert("marker");
        map.remove(that.marker);
      }
      map.setCenter([position.coords.longitude, position.coords.latitude]);
      that.marker = new AMap.Marker({
        position: [position.coords.longitude, position.coords.latitude]
      });
      that.marker.setMap(map);
    },
    onErrorGet: function(error) {
      alert("code: " + error.code + "\n" + "message: " + error.message + "\n");
    },

    watchPosition: function() {
      let that = this;
      console.log("start locating");
      console.log(this.optionsWatch);

      this.watchID = navigator.geolocation.watchPosition(
        that.onSuccessWatch,
        that.onErrorWatch,
        that.optionsWatch
      );
    },
    onSuccessWatch: function(position) {
      this.loadMarker(position.coords.latitude, position.coords.longitude);
      // this.loadMarker(116.480983, 39.989628);
      // alert(
      //   "Latitude: " +
      //     position.coords.latitude +
      //     "\n" +
      //     "Longitude: " +
      //     position.coords.longitude +
      //     "\n" +
      //     "Altitude: " +
      //     position.coords.altitude +
      //     "\n" +
      //     "Accuracy: " +
      //     position.coords.accuracy +
      //     "\n" +
      //     "Altitude Accuracy: " +
      //     position.coords.altitudeAccuracy +
      //     "\n" +
      //     "Heading: " +
      //     position.coords.heading +
      //     "\n" +
      //     "Speed: " +
      //     position.coords.speed +
      //     "\n" +
      //     "Timestamp: " +
      //     position.timestamp +
      //     "\n"
      // );
      let that = this;
      let map = that.map;

      if (that.marker) {
        // alert("marker");
        map.remove(that.marker);
      }

      map.setCenter([position.coords.longitude, position.coords.latitude]);
      that.marker = new AMap.Marker({
        position: [position.coords.longitude, position.coords.latitude]
      });
      that.marker.setMap(map);
    },
    onErrorWatch: function(error) {
      alert("code: " + error.code + "\n" + "message: " + error.message + "\n");
    },
    clearPositionWatch: function() {
      let that = this;
      console.log("stop locating");
      navigator.geolocation.clearWatch(that.watchID);
    },
    testPosition: function() {
      let that = this;
      console.log(this.map);
      console.log(this.marker);
      console.log("marker");
      this.marker = new AMap.Marker({
        position: [116.480983, 39.989628]
      });
      this.marker.setMap(that.map);
    },
    coordinateConvert: function(longitude, latitude) {
      $.ajax({
        url:
          "http://restapi.amap.com/v3/assistant/coordinate/convert?key=056fdd2f790e0335f31907e00c8bde44&locations=" +
          "121.609176" +
          "," +
          "31.279304" +
          "&coordsys=gps",
        type: "get",

        success: function(data) {
          console.log(data);
          console.log(data.locations.split(",")[0]);
        }
      });
    }
  }
};
</script>


<style>
.mymap {
  height: 90%;
  width: 100%;
  position: absolute;
  top: 45px;
  bottom: 0px;
  z-index: 0;
  overflow: hidden;
  /* word-break: break-all; */
  margin: 0px;
  left: 0px;
}
</style>




