<template>
  <div class="hello">
    <mt-header fixed title="列表"></mt-header>
    <div class="button"><mt-button plain type="primary" size="large" @click.native="toTrack">地图</mt-button></div>
    <div class="button"><mt-button plain type="primary" size="large" @click.native="toTrack3">二维码</mt-button></div>
    <div class="button"><mt-button plain type="primary" size="large" @click.native="toTrack4">拍照</mt-button></div>
    <div class="button"><mt-button plain type="primary" size="large" @click.native="toTrack5">摇一摇</mt-button></div>
    <div class="button"><mt-button plain type="primary" size="large" @click.native="toTrack2">测试</mt-button></div>
        
  </div>
</template>

<script>
import { Button, Header } from "mint-ui";

export default {
  name: "HelloWorld",
  data() {
    return {};
  },
  methods: {
    toTrack() {
      this.$router.push("/Map");
    },
    toTrack2() {
      this.$router.push("/Test");
    },
    toTrack3() {
      this.$router.push("/QRcode");
    },
    toTrack4() {
      this.$router.push("/Picture");
    },
    toTrack5() {
      this.$router.push("/Shake");
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.button {
  margin: 1rem;
}
</style>



