<template>
<div >
    <mt-header fixed >
    <mt-button icon="back" slot="left" @click.native="back">返回</mt-button>
    </mt-header>
    <div id="shakeImage">  
    </div>
</div>
</template>

<script>
export default {
  data() {
    return {
      watchID: "",
      accelerometerOptions: { frequency: 350 } // Update every 3 seconds
    };
  },
  mounted() {
    this.watchAcceleration();
  },
  methods: {
    back() {
      this.clearAccelerationWatch();
      this.$router.go(-1);
    },
    watchAcceleration: function() {
      //   alert("start");
      let that = this;
      this.watchID = navigator.accelerometer.watchAcceleration(
        that.accelerometerSuccess,
        that.accelerometerError,
        that.accelerometerOptions
      );
    },
    accelerometerSuccess: function(acceleration) {
      //   alert(
      //     "Acceleration X: " +
      //       acceleration.x +
      //       "\n" +
      //       "Acceleration Y: " +
      //       acceleration.y +
      //       "\n" +
      //       "Acceleration Z: " +
      //       acceleration.z +
      //       "\n" +
      //       "Timestamp: " +
      //       acceleration.timestamp +
      //       "\n"
      //   );
      if (
        Math.abs(acceleration.x) > 13 ||
        Math.abs(acceleration.y) > 13 ||
        Math.abs(acceleration.z) > 13
      ) {
        alert("监测到摇动");
      }
    },
    accelerometerError: function() {
      alert("onError!");
    },
    clearAccelerationWatch: function() {
      let that = this;
      //   alert("stop");
      navigator.accelerometer.clearWatch(that.watchID);
    }
  }
};
</script>


<style>
#shakeImage {
  width: 128px;
  height: 128px;
  background: url(../../static/shake.png) no-repeat;
  position: absolute;
  top: calc(50% - 64px);
  left: calc(50% - 64px);
}
</style>




