<template>
<div>
<mt-header fixed title="二维码">
    <mt-button icon="back" slot="left" @click.native="back">返回</mt-button>
</mt-header>

<div  id="backgroundscan" @click="barcodeScanner"></div>
</div>
  
</template>



<script>
export default {
  methods: {
    back() {
      this.$router.go(-1);
    },
    barcodeScanner: function() {
      let that = this;
      cordova.plugins.barcodeScanner.scan(that.onSucess, that.onError, {
        preferFrontCamera: false, // iOS and Android
        showFlipCameraButton: true, // iOS and Android
        showTorchButton: true, // iOS and Android
        torchOn: true, // Android, launch with the torch switched on (if available)
        saveHistory: true, // Android, save scan history (default false)
        prompt: "Place a barcode inside the scan area", // Android
        resultDisplayDuration: 500, // Android, display scanned text for X ms. 0 suppresses it entirely, default 1500
        formats: "QR_CODE,PDF_417", // default: all but PDF_417 and RSS_EXPANDED
        // orientation: "landscape", // Android only (portrait|landscape), default unset so it rotates with the device
        disableAnimations: true, // iOS
        disableSuccessBeep: false // iOS and Android
      });
    },
    onSucess: function(result) {
      alert(
        "We got a barcode\n" +
          "Result: " +
          result.text +
          "\n" +
          "Format: " +
          result.format +
          "\n" +
          "Cancelled: " +
          result.cancelled
      );
    },
    onError: function(error) {
      alert("Scanning failed: " + error);
    }
  }
};
</script>



<style>
#backgroundscan {
  width: 128px;
  height: 128px;
  background: url(../../static/scan.png) no-repeat;
  position: absolute;
  top: calc(50% - 64px);
  left: calc(50% - 64px);
}
</style>




