<template>
<div id="image">
  <mt-header fixed >
    <mt-button icon="back" slot="left" @click.native="back">返回</mt-button>
    <mt-button icon="more" slot="right" @click.native="actionSheet"></mt-button>
  </mt-header>
  <div v-show="ifShow" id="background" @click="cameraTakePicture"></div>
  <div  v-show="!ifShow" id="myImage"><img id="detailimg"></div>
  
  <mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>
</div>
  
</template>



<script>
import { Actionsheet } from "mint-ui";

export default {
  data() {
    return {
      ifShow: true,
      actions: [
        {
          name: "拍照",
          method: this.cameraTakePicture
        },
        {
          name: "从相册中选择",
          method: this.cameraGetPicture
        }
      ],
      sheetVisible: false
    };
  },
  methods: {
    actionSheet: function() {
      this.sheetVisible = true;
    },
    back() {
      this.$router.go(-1);
    },
    // getPicture: function() {
    //   return;
    // },
    // takePicture: function() {
    //   return;
    // },
    cameraTakePicture: function() {
      navigator.camera.getPicture(this.onSuccessTake, this.onFailTake, {
        quality: 50,
        destinationType: Camera.DestinationType.DATA_URL
      });
    },
    onSuccessTake: function(imageData) {
      this.ifShow = false;
      var image = document.getElementById("detailimg");
      image.src = "data:image/jpeg;base64," + imageData;
    },

    onFailTake: function(message) {
      this.ifShow = true;
      alert("Failed because: " + message);
    },
    cameraGetPicture: function() {
      navigator.camera.getPicture(this.onSuccessGet, this.onFailGet, {
        quality: 50,
        destinationType: Camera.DestinationType.DATA_URL,
        sourceType: Camera.PictureSourceType.PHOTOLIBRARY
      });
    },
    onSuccessGet: function(imageURL) {
      this.ifShow = false;
      var image = document.getElementById("detailimg");
      image.src = "data:image/jpeg;base64," + imageURL;
    },
    onFailGet: function(message) {
      this.ifShow = true;
      alert("Failed because: " + message);
    }
  }
};
</script>


<style>
#background {
  width: 128px;
  height: 128px;
  background: url(../../static/picture.png) no-repeat;
  position: absolute;
  top: calc(50% - 64px);
  left: calc(50% - 64px);
}
#myImage {
  width: 100%;
  height: calc(100% - 40px);
  position: absolute;
  top: 40px;
  left: 0;
}
#detailimg {
  width: 100%;
  height: 100%;
}
</style>




